import React from 'react';
import { useNavigate } from 'react-router-dom';
import './Tarifs.css';

const Tarifs = () => {
  const navigate = useNavigate();

  return (
    <div className="tarifs-page">
      <h1 className="tarifs-title">Billetterie TEDx 2025</h1>
      <div className="tarifs-intro" style={{
        marginBottom: '3rem',
        textAlign: 'center',
        color: '#fff',
        maxWidth: '600px'
      }}>
        <p style={{ fontSize: '1.1rem', lineHeight: '1.6', marginBottom: '1rem' }}>
           Rserve ta place pour plonger au cur de l'impossible !
        </p>
        <p style={{ fontSize: '0.95rem', opacity: 0.9 }}>
          Places limites  | Jeudi 27 novembre 2025  18h au Thtre Rousseau
        </p>
      </div>

      <div className="tarifs-grid">
        <div className="tarif-card">
          <h2 style={{ color: '#dc2626', marginTop: 0 }}> Classique</h2>
          <div style={{ fontSize: '1.3rem', fontWeight: 'bold', color: '#fff', marginBottom: '1rem' }}>
            <div>4 <span style={{ opacity: 0.7, fontSize: '0.9rem' }}>CentraleSuplec</span></div>
            <div>8 <span style={{ opacity: 0.7, fontSize: '0.9rem' }}>Extrieurs</span></div>
          </div>
          <div style={{ color: '#e5e7eb', lineHeight: '1.6', fontSize: '0.95rem', marginBottom: '1.5rem' }}>
            <p> Accs  tous les talks</p>
            <p> Buffet  l'entracte</p>
          </div>
          <button 
            onClick={() => navigate('/acheter')}
            style={{
              width: '100%',
              padding: '10px',
              backgroundColor: '#dc2626',
              color: '#fff',
              border: 'none',
              borderRadius: '6px',
              cursor: 'pointer',
              fontWeight: 'bold',
              fontSize: '0.95rem'
            }}
          >
            Acheter
          </button>
        </div>

        <div className="tarif-card" style={{ border: '2px solid #dc2626', position: 'relative' }}>
          <div style={{
            position: 'absolute',
            top: '-12px',
            left: '50%',
            transform: 'translateX(-50%)',
            backgroundColor: '#dc2626',
            color: '#fff',
            padding: '4px 16px',
            borderRadius: '20px',
            fontSize: '0.8rem',
            fontWeight: 'bold'
          }}>
             Recommand
          </div>
          <h2 style={{ color: '#dc2626', marginTop: '1.5rem' }}> VIP</h2>
          <div style={{ fontSize: '1.3rem', fontWeight: 'bold', color: '#fff', marginBottom: '1rem' }}>
            <div>9 <span style={{ opacity: 0.7, fontSize: '0.9rem' }}>CentraleSuplec</span></div>
            <div>15 <span style={{ opacity: 0.7, fontSize: '0.9rem' }}>Extrieurs</span></div>
          </div>
          <div style={{ color: '#e5e7eb', lineHeight: '1.6', fontSize: '0.95rem', marginBottom: '1.5rem' }}>
            <p> Accs  tous les talks</p>
            <p> Buffet exclusif avec les speakers</p>
            <p> Buffet  l'entracte</p>
            <p> Exprience VIP premium</p>
          </div>
          <button 
            onClick={() => navigate('/acheter')}
            style={{
              width: '100%',
              padding: '10px',
              backgroundColor: '#dc2626',
              color: '#fff',
              border: 'none',
              borderRadius: '6px',
              cursor: 'pointer',
              fontWeight: 'bold',
              fontSize: '0.95rem'
            }}
          >
            Acheter
          </button>
        </div>
      </div>

      <div style={{
        marginTop: '3rem',
        padding: '2rem',
        backgroundColor: 'rgba(220, 38, 38, 0.1)',
        borderLeft: '4px solid #dc2626',
        borderRadius: '6px',
        maxWidth: '600px',
        color: '#fff'
      }}>
        <p style={{ marginTop: 0, fontSize: '0.95rem', lineHeight: '1.6' }}>
          <strong> Lieu :</strong> Thtre Rousseau (btiment Bouygues)<br />
          <strong> Date :</strong> Jeudi 27 novembre 2025<br />
          <strong> Heure :</strong> 18h00<br />
          <strong> Places :</strong> Limites - rserve vite !
        </p>
      </div>
    </div>
  );
};

export default Tarifs;


